<template>
    <div class="service-list">
        <!-- 查询 -->
        <div class="mb10">
            <div class="dib w200">
                <el-input v-model="query.name" placeholder="企业名称/代码/联系人"></el-input>
            </div>
            <el-select v-model="query.industry_type_id" class="w200" placeholder="请选择行业">
                <el-option value="" label="全部行业"></el-option>
                <el-option v-for="(item,i) in industryTypes" :key="i" :value="item.value" :label="item.label"></el-option>
            </el-select>
            <el-select v-model="query.state" class="w200" placeholder="请选择状态">
                <el-option value="" label="全部状态"></el-option>
                <el-option v-for="(item,i) in serviceStates" :key="i" :value="item.value" :label="item.label"></el-option>
            </el-select>
            <label></label>
            <el-date-picker v-model="dateRange" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" @change="changeDageRange">
            </el-date-picker>
            <el-button type="primary" @click="onSearch">查询</el-button>
            <el-button @click="onReset">清空</el-button>
        </div>
        <!-- 按钮 -->
        <div class="mb10">
        	<el-button type="primary" @click="applyOpenService">申请开通</el-button>
        	<el-button @click="exportList">导出</el-button>
        </div>

        <!-- 列表 -->

        <el-table :data="tableParams.list" header-align="center" border header-row-class-name="header-center">
			<el-table-column label="企业名称" prop="customer_name">
				<template slot-scope="scope">
					<span class="text-link" @click="toDetail(scope.row)">{{scope.row.customer_name}}</span>
				</template>
			</el-table-column>
			<el-table-column label="社会信用代码" prop="social_credit_code"></el-table-column>
			<el-table-column label="联系人" prop="contact_name"></el-table-column>
			<el-table-column label="所属行业" prop="industry_type_name"></el-table-column>
			<el-table-column label="所在地" prop="city_id">
				<template slot-scope="scope">
					<!-- 前端先写一个 后面根据后端提供修改 -->
					<span>福州</span>
				</template>
			</el-table-column>
			<el-table-column label="申请时间" prop="create_time">
                <template slot-scope="scope">
                    <span>{{scope.row.create_time | dateFormat}}</span>
                </template>         
            </el-table-column>
			<el-table-column label="状态" prop="service_state">
				<template slot-scope="scope">
					<span>{{serviceStateText[scope.row.service_state]}}</span>
				</template>
			</el-table-column>
			<el-table-column label="操作" >
				<template slot-scope="scope">
					<el-button type="text" @click="toDetail(scope.row)" size="mini">查看</el-button>
					<el-button type="text" @click="toEdit(scope.row)" size="mini" v-if="scope.row.service_state != 1">编辑</el-button>
					<el-button type="text" @click="doDelete(scope.row)" size="mini" v-if="scope.row.service_state != 1">删除</el-button>
				</template>
			</el-table-column>
    	</el-table>
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tableParams.currentPage" :page-sizes="tableParams.pageSizeList" :page-size="tableParams.pageSize" :layout="tableParams.layout" :total="tableParams.totalCount" v-if="tableParams.totalCount" class="mt15">
		</el-pagination>
    </div>
</template>
<script>
import tableMixin from '@/mixins/tableMixin'
import * as constants from './constants.js'
import { serviceStates } from '@/views/const'

export default {
    data() {
        return {
            query: constants.initQuery(),
            industryTypes: [
                {value: 1, label: '行业1'},
                {value: 2, label: '行业2'}
            ],
            serviceStates,
            dateRange: null,
            serviceStateText : {
            	1: '通过',
            	2: '待审核',
            	3: '审核拒绝'
            }
        }
    },
    mixins: [tableMixin],
    methods: {
   
    	_loadList () {
            let params = this.assignQuery(this.query)
    		this.$api.service.getServiceList(params).then(data => {
    			this.tableParams.list = data.rows
    			this.tableParams.totalCount = data.total
    		})
    	},
    	onReset() {
    		this.dateRange = null
            this.query = constants.initQuery()
            this.onSearch()
    	},
    	changeDageRange(e){
    		this.query.start_time = e ? e[0] || '' : ''
    		this.query.end_time = e ? e[1] || '' : ''
    	},
    	// 申请开通
    	applyOpenService(){
    		this.$router.push({name: 'serviceNew'})
    	},
    	exportList (){
    		console.log('导出数据')
    	},
    	// 查看
    	toDetail (service){
    		this.$router.push({name: 'serviceDetail', params:{serviceId: service.id }})
    	},
    	// 编辑
    	toEdit (service){
    		this.$router.push({name: 'serviceEdit', query: {id: service.id }})
    	},
    	// 删除
    	doDelete (service){
    		this.$api.service.deleteService(service.id).then(data => {
    			if (this.tableParams.pageNum > 1 && this.tableParams.list.length == 1) {
    				--this.tableParams.pageNun
    			}
                this.$message('服务删除成功');
    			this._loadList()
    		})
    	},
    }

}
</script>


